  .vertical-products {
    .widget-column {
        .title {
            .widget-title {
                &::before {
                    content: "";
                    left: 0;
                    position: absolute;
                    width: 100%;
                    height: 0.0625rem;
                    bottom: 0.34375rem;
                    background-color: #404040;
                }
                &::after {
                    content: "";
                left: 0;
                position: absolute;
                width: 2.5rem;
                height: 0.125rem;
                background-color: #9c9c9c;
                bottom: 0.3125rem;
                }
                
            
