.popular-recipes-box .recipes-contain .gallery .gallery-section .gallery__item {
  overflow: hidden;
}
.popular-recipes-box .recipes-contain .gallery .gallery-section .gallery__item:nth-child(1) {
  overflow: hidden;
  grid-row: 1/span 2;
  grid-column: 1/span 2;
