        }
      }
      .gallery {
        .gallery-section {
          grid-column: full-start / full-end;
          display: grid;
          /*grid-template-columns: repeat(8, 1fr);
            grid-template-rows: repeat(7, 5vw);*/
          grid-template: repeat(5, 5vw)/repeat(8, 1fr);
          grid-gap: 1.5rem;
          padding: 1.5rem; 
          img {
            max-width: 100%;
            height: auto;
            -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    will-change: transform;
        }
        .gallery__item {
          overflow: hidden;
          
          &:nth-child(1) {
            overflow: hidden;
            grid-row: 1 / span 2;
            grid-column: 1 / span 2; }
            &:nth-child(2) {
              grid-row: 1 / span 3;
              grid-column: 3 / span 3; }
              &:nth-child(3) {
            grid-row: 1/span 2;
            grid-column: 8/8; }
          
            &:nth-child(4) {
              grid-row: 3 / span 3;
              grid-column: 1 / span 2; }
              &:nth-child(5) {
                grid-row: 4 / span 2;
                grid-column: 3 / span 2; }
                &:nth-child(6) {
              grid-row: 4/6;
              grid-column: 5/8;}
              &:nth-child(7) {
              grid-row: 1/4;
              grid-column: 6/span 2; }
              &:nth-child(8) {
                grid-row: 3 / span 3;
                grid-column: 8 / -1;
        }
         &:hover {
img {
  -webkit-transform: scale(1.02);
    -ms-transform: scale(1.02);
    transform: scale(1.02);
}
         }
          
               }
        
          .gallery__img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            border-radius: 0px;
        }
        }
      }
    }
}

