/***************************************
*  Cart page styles
****************************************/
.cart-wrapper {
  .mat-card {
    text-align: center;
    .cart-table.mat-table {
      display: block;
      overflow-x:auto;
      .mat-row{
        min-height: 100px;
        min-height: 100px;
  border-bottom: 1px solid $grey2;
        .product-name{
          color: inherit;
          text-decoration: none;
          font-weight: 500;
      }
      .grand-total{
        font-weight: 500;

    }
    span:nth-child(3){
      font-size: 16px;
  }

  input {
    width: 80px;
    text-align: center;
    background-color: $grey2;
    height: 35px;
    border: 1px solid $grey6;
  }
  .price {
    font-size: 21px;
      font-weight: 500;
  }
  .total {
    font-size: 21px;
      font-weight: 500;
      color: $theme-main-red;
  }
    }
    .mat-cell, .mat-header-cell {
      flex: 1;
      overflow: hidden;
      word-wrap: break-word;
      font-size: 14px;
      img{
        width: 100px;
        cursor: pointer;
    }
    mat-icon {
      cursor: pointer;
    }
  }
    }
    }

}

.mat-row, .mat-header-row {
  display: flex;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  align-items: center;
  min-height: 48px;
  padding: 0 24px;
  min-width: 870px;
}


/***************************************
*  Compare page styles
****************************************/
.compare-component {
  .table-responsive {
    display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
    table {
      width: 100%;
      border: 1px solid $grey2;
      thead {
        .th-compare {
          td {
            padding: 15px;
            vertical-align: top;
            border-bottom: 1px solid $grey2;
            border-top: 1px solid $grey2;
            border-right: 1px solid $grey2;
            background: $gray-lighter;
            font-weight: 600;
              font-size: 14px;
          }
          a {
            cursor: pointer;
          }
        }
      }
      tbody {
        tr {
          th {
            padding: 15px;
            vertical-align: top;
            border-bottom: 1px solid $grey2;
            border-top: 1px solid $grey2;
            border-right: 1px solid $grey2;
            background: $gray-lighter;
            text-align: left;
          }
          .product-name {
            width: 15%;
            font-size: 14px;
          }
          td {
            .description-compare {
              font-size: 14px;
              line-height: 22px;
            }
          }
        }
      }
    }
  }
  img.img-fluid.mb-4 {
    max-width: 100px;
  }
  .empty-cart-cls {
    h4 {
      font-weight: 500;
      color: $grey5;
    }
    h3 {
      text-align: center;
      }
  }
}


.compare-component .table-responsive .table tbody tr td, .compare-component .table-responsive .table tbody tr th {
  padding: 15px;
  vertical-align: top;
  border-top: 1px solid $grey2;
}



/***************************************
*  Details page styles
****************************************/
.details-wrapper {
  .product-image {
    img {
      max-width: 100%;
    }
  }
  .product-right {
    padding: 0 30px 0 25px;
    .product-details {
      padding-bottom: 15px;
        border-bottom: 1px dashed $grey-light;
      h2 {
        margin-bottom: 10px;
      }
      h4 {
        del {
          color: $grey;
          font-size: 17px;
        }
        span {
          padding-left: 5px;
          color: $theme-main-red;
          font-size: 24px;
        }
      }
      .avalibility {
        span {
          color: $theme-main-red;
          font-weight: 500;
        }
        
      }
      p.description {
        line-height: 26px;
      }
      h3.price {
        font-size: 26px;
        font-weight: 400;
        margin: 10px 0;
      }


    }
    .quantity {
      margin-top: 10px;

      span {
        margin-bottom: 7px;
        display: block;
      }
      input {
        width: 80px;
        text-align: center;
        background-color: $white-dark;
        height: 35px;
        border: 1px solid $grey3;
      }
    }
    .red {
      color: #f33927;
      font-weight: 700;
    }
  }
  .info-bar {
    border: 1px solid $grey-light;
    padding: 0 20px;
    margin-top: 0px !important;
    .mat-icon{
      color: $font-color;
      font-size: 41px;
      margin-right: 15px;
      line-height: 23px;
    }
    .content {
      p {
        text-transform: capitalize;
        font-weight: 500;
      }
    }
    .mat-card {
      box-shadow: none !important;
      padding: 20px 0;
      border-bottom: 1px solid $grey-light;

   }
   .mat-card.no-border {
    border-bottom: none;
  }
  }
  .review-wrapper {
    padding-right: 30px;
    margin-top: 30px;
    .full-desc {
      padding: 20px;
      p {
        line-height: 26px;
      }
      ul {
        margin-top: 15px;
        padding-left: 20px;
        li {
          margin-bottom: 7px;
        }
      }
    }
    .Reviews {
      .reviews {
        .name {
          font-weight: 500;
         }
         p {
          line-height: 22px;
          white-space: normal;
        }
        .text {
          margin-top: 10px;
          }
          .mat-list-item {
            .mat-list-item-content {
              align-items: start !important;
            }
          }
      }
      .writte-reviews {
        .mat-form-field {
          width: 100%;
         }
      }
    }

  }
  .products {
    padding-right: 30px;
    .title {
      margin-bottom: 15px;
    position: relative;
    padding-bottom: 1.25rem;
    &::before {
      content: "";
      left: 0;
      position: absolute;
      width: 100%;
      height: 0.0625rem;
      bottom: 0.34375rem;
      background-color: #f6f6f6;
  }
  &::after {
    content: "";
    left: 0;
    position: absolute;
    width: 2.5rem;
    height: 0.125rem;
    background-color: #222222;
    bottom: 0.3125rem;
  }
    }
  }
    
    .sale-widget {
      height: 14%;
    margin-top: 25px;
      padding: 15px 20px;
      text-align: center;
      background-color: var(--theme-deafult);
      .sale-widget1 {
        color: $white;
        font-size: 36px;
         font-weight: 600;
         display: block;
         letter-spacing: .5;
      }
      .sale-widget2 {
        color: $white;
        font-size: 18px;
         font-weight: 600;
         display: block;
         letter-spacing: .5;
      }
      .discount {
        display: inline-block;
        color: $grey-dark;
        font-size: 77px;
        font-weight: 600;
        position: relative;
        letter-spacing: .5;
      }
      .percent {
        display: inline-block;
        line-height: 31px;
        span {
          display: block;
          font-size: 3.111em;
          font-weight: 500;
        }
      }
      p {
        color: $white;
        line-height: 24px;
        font-weight: 300;
        font-size: 15px;
      }
    }
  
  
  .small-carousel {
    position: relative;
    margin-top: 10px;
    .swiper-container {
      height: 180px;
      .swiper-slide {
        mat-card {
          max-height: 120px;
          min-height: 120px;
          img {
            max-width: 110px;
            margin-bottom: 12px;
            display: block;
            margin-left: auto;
            margin-right: auto;
            cursor: pointer;
          }
        }
      }
      .swiper-button-prev {
      position: absolute;
      top: 32%;
      left: 0px;;
      }
      .swiper-button-next {
        position: absolute;
      top: 32%;
      right: 0px;;
      }
    }


  }
  .sale-baner {
background: transparent;
  }
  .mat-expansion-panel {
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
    border: 1px solid #dddddd;
    box-shadow: none !important;
    margin-bottom: 20px;
    background: transparent !important;
}
}

